import { useState } from 'react';

import {  NavLink } from 'react-router-dom';
import { useAuth } from '../../../context/AuthContext.jsx'
import NavItem from '../NavItem/NavItem.jsx';
import Button from '../../Button/Button.jsx';
import TweetModal from '../../../components/Modal/TweetModal/TweetModal.jsx';

import logo from '../../../assets/icons/logo.png';
import logoutImg from '../../../assets/icons/nav/nav_signout.png';
import tweetIcon from '../../../assets/icons/nav/nav_btn.png'
import styles from './NavBarContainer.module.scss';


export default function NavBarContainer({ role, page }) {
    const [ isModalOpen, setIsModalOpen ] = useState(false);
    const { logout, currentUser } = useAuth();

    const handleOpenModal = () => {
        setIsModalOpen(true);
    };

    const handleCloseModal = () => {
        setIsModalOpen(false);
    };
    
    return (
        <>
            <div className={styles.navBarContainer}>
                <img className={styles.img} src={logo} alt="logo" />
                <div className={styles.main}>
                    {role === 'user' && (
                        <>
                            {page === 'main' && (
                                <>
                                    <NavLink to="/main">
                                        <NavItem iconStyle={'iconHome'} altName="main" title="" isActive />
                                    </NavLink>

                                    <div className={styles.tweetBtn}>
                                        <img
                                            className={styles.tweetIcon}
                                            src={tweetIcon}
                                            alt="tweetIcon"
                                            onClick={handleOpenModal}
                                        />
                                    </div>

                                    <NavLink to={`/user/${currentUser?.id}/tweet`}>
                                        <NavItem iconStyle={'iconUser'} altName="user" title="" />
                                    </NavLink>

                                    <NavLink to="/setting">
                                        <NavItem iconStyle={'iconSetting'} altName="setting" title="" />
                                    </NavLink>
                                </>
                            )}
                            {page === 'userPage' && (
                                <>
                                    <NavLink to="/main">
                                        <NavItem iconStyle={'iconHome'} altName="main" title="" />
                                    </NavLink>

                                    <div className={styles.tweetBtn}>
                                        <img
                                            className={styles.tweetIcon}
                                            src={tweetIcon}
                                            alt="tweetIcon"
                                            onClick={handleOpenModal}
                                        />
                                    </div>

                                    <NavLink to={`/user/${currentUser?.id}/tweet`}>
                                        <NavItem iconStyle={'iconUser'} altName="user" title="" isActive />
                                    </NavLink>

                                    <NavLink to="/setting">
                                        <NavItem iconStyle={'iconSetting'} altName="setting" title="" />
                                    </NavLink>
                                </>
                            )}
                            {page === 'setting' && (
                                <>
                                    <NavLink to="/main">
                                        <NavItem iconStyle={'iconHome'} altName="main" title="" />
                                    </NavLink>

                                    <div className={styles.tweetBtn}>
                                        <img
                                            className={styles.tweetIcon}
                                            src={tweetIcon}
                                            alt="tweetIcon"
                                            onClick={handleOpenModal}
                                        />
                                    </div>

                                    <NavLink to={`/user/${currentUser?.id}/tweet`}>
                                        <NavItem iconStyle={'iconUser'} altName="user" title="" />
                                    </NavLink>

                                    <NavLink to="/setting">
                                        <NavItem iconStyle={'iconSetting'} altName="setting" title="" isActive />
                                    </NavLink>
                                </>
                            )}
                            <div className={styles.btn}>
                                <Button title="" size="navTweet" isAction onClick={handleOpenModal}></Button>
                            </div>
                        </>
                    )}
                    {role === 'admin' && (
                        <>
                            {page === 'admintweet' && (
                                <>
                                    <NavLink to="/admin/tweets">
                                        <NavItem iconStyle={'iconHome'} altName="main" title="" isActive />
                                    </NavLink>

                                    <NavLink to="/admin/users">
                                        <NavItem iconStyle={'iconUser'} altName="user" title="" />
                                    </NavLink>
                                </>
                            )}
                            {page === 'adminuser' && (
                                <>
                                    <NavLink to="/admin/tweets">
                                        <NavItem iconStyle={'iconHome'} altName="main" title="" />
                                    </NavLink>

                                    <NavLink to="/admin/users">
                                        <NavItem iconStyle={'iconUser'} altName="user" title="" isActive />
                                    </NavLink>
                                </>
                            )}
                        </>
                    )}
                </div>
                <div className={styles.logOut} onClick={() => logout()}>
                    <img className={styles.img} src={logoutImg} alt="log-out" />
                    <h5 className={styles.btnName}></h5>
                </div>
                {isModalOpen && <TweetModal handleCloseModal={handleCloseModal} />}
            </div>
        </>
    );
}
